<template>
  <div class="container">
    <button class="btn btn-primary" @click="showRes = !showRes">
      {{ btnText }}
    </button>
    <hr />
    <h3 v-show="showRes" class="alert alert success">
      Your luck: {{ mathLuck() }} %
    </h3>
    <hr />
    <app-progress-gold :val="sum" :max="maxNumbers * 5"> </app-progress-gold>
    <hr />
    <transition name="slide">
      <h3 v-show="showRes" class="alert alert success">
        Your profit: {{ sum }}
      </h3>
    </transition>
    <hr />
    <app-Progress :val="numbers.length" :max="maxNumbers"> </app-Progress>

    <hr />
    <button class="btn btn-success" @click="addNumber" :disabled="done">
      Add number
    </button>
    <hr />
    <ul class="list-group">
      <li class="list-group-item" v-for="number in numbers" :key="number.id">
        {{ number }}
      </li>
    </ul>
  </div>
</template>

<script>
import Progress from "./components/progress-bar.vue";
import ProgressGold from "./components/progress-bar-lucky.vue";

export default {
  data() {
    return {
      showRes: true,
      numbers: [],
      maxNumbers: 10,
    };
  },
  methods: {
    addNumber() {
      if (!this.done) {
        let rnd = Math.floor(Math.random() * 11) - 5;
        this.numbers.push(rnd);
      }
    },
    mathLuck() {
      let sumLuck = 0;
      sumLuck = (this.sum / (this.maxNumbers * 5)) * 100;
      let roundedSumLuck = sumLuck.toFixed(2);
      if (roundedSumLuck >= 0) {
        roundedSumLuck;
      } else {
        roundedSumLuck = 0;
      }
      return roundedSumLuck;
    },
  },
  computed: {
    sum() {
      let sum = 0;
      for (let i = 0; i < this.numbers.length; i++) {
        sum += this.numbers[i];
      }
      return sum;
    },
    btnText() {
      return this.showH2 ? "Hide result" : "Show result";
    },
    done() {
      return this.numbers.length >= this.maxNumbers;
    },
  },
  components: {
    appProgress: Progress,
    appProgressGold: ProgressGold,
  },
};
</script>

<style scoped>
.btn.btn-primary {
  margin-top: 20px;
}

.slide-enter {
  opacity: 0;
}
.slide-enter-active {
  transition: opacity 0.5s;
}
.slide-leave-active {
  transition: opacity 0.5s;
}
.slide-leave-to {
  opacity: 0;
}
</style>
